import addrecode from '../../components/addRecode.vue'
import common from '../../common/common.js'
export default {
	data() {
		return {
			add: true,
			id: '', //警告id
			info: {}, //已完成详情
			replenish: [], //添加补充
			warnNo: '', //报警编号
			picUrl: '',
			page: 1,
			pageSize: 10,
		}
	},
	components: {
		addrecode
	},
	onLoad(options) {
		var id = options.id;
		this.id = id;
		var status = uni.getStorageSync('status');
		if (status == 2) {
			uni.redirectTo({
				url: '../police/police?id=' + id
			})
		}
		this.picUrl = common.picUrl;
		this.getInfo(id);
	},
	methods: {
		// 获取已完成信息
		getInfo(id) {
			var data = {
				page: 1,
				pageSize: 1,
				recordId: id,
			}
			var that = this;
			common.getData('/muqiang/invitation/getWarnList', data, (res) => {
				that.info = res.pageInfo.list[0];
				var warnNo = res.pageInfo.list[0].warn_no;
				that.warnNo = warnNo;
				that.getAdd(warnNo)
			})
		},
		getAdd(warnNo) {
			var that = this;
			var data = {
				page: 1,
				pageSize: that.pageSize,
				warnNo: warnNo,
			}
			var that = this;
			common.getData('/muqiang/invitation/getDetailRepairList', data, (res) => {
				this.replenish = res.pageInfo.list;
				// console.log(res)
			})
		},
		// 查看大图片
		big(e) {
			console.log(e.currentTarget.id)
			var imgs = e.currentTarget.id;
			var arr = [];
			arr.push(imgs)
			uni.previewImage({
				current: imgs,
				urls: arr,
			});
		},
		// 下拉加载补充
		lower() {
			var that = this;
			var pageSize = that.pageSize;
			pageSize = pageSize + 10;
			that.pageSize = pageSize;
			var warnNo = that.warnNo;
			that.getAdd(warnNo);
		},
		// 添加按钮
		adds() {
			this.add = false;
		},
		// 子组件传递过来
		save(e) {
			var id = this.id;
			this.getInfo(id)
			this.add = e;
		}
	}
}
